import React from "react";

import qs from "qs";
import { useNavigate } from "react-router-dom";
import NoName from "./NotFound";

import { list } from "../components/Sort";
import Categories from "../components/Categories";
import Sort from "../components/Sort";
import PelmeniBlock from "../components/Pelmeni-Block";
import Skeleton from "../components/Skeleton";
import Pagination from "../components/Pagination";
import { useDispatch, useSelector } from "react-redux";
import {
  setCategoryId,
  setCurrentPage,
  setFilter,
} from "../redux/slices/filterSlice";
import { fetchProducts } from "../redux/slices/itemsSlice";

const Home = ({ searchValue }) => {
  const navigate = useNavigate();
  //const [isLoading, setIsLoading] = React.useState(true);
  const { categoryId, currentPage } = useSelector((state) => state.filterSlice); //c      
  const sortType = useSelector((state) => state.filterSlice.sort.sortProperty);

  const { items, webStatus } = useSelector((state) => state.itemsSlice);
  // useSelector -    

  const dispatch = useDispatch();
  // useDispatch -    id     state  reducer,  Slice   value - categoryId
  //  - 

  const onChangeCategory = (id) => {
    dispatch(setCategoryId(id));
  };

  const onChangePageNumber = (number) => {
    dispatch(setCurrentPage(number));
  };

  //  URL- ,     , 
  //      
  const isMounted = React.useRef(false);

  //  ,   useEffect -        URL
  const isAxios = React.useRef(false); //   
  //  ,       - 

  const axiosProducts = () => {
    //await -     async,        ,     
    const category = categoryId > 0 ? `category=${categoryId}` : "";
    const sort = sortType.replace("-", "");
    const sortOrder = sortType.includes("-") ? "asc" : "desc";
    const search = searchValue ? `search=${searchValue}` : "";

    // await Axios.get(
    //   //await -     ,    fetch-
    //   //    - ,  
    //   `https://66028e549d7276a755538691.mockapi.io/items?page=${currentPage}&limit=4&${category}&sortBy=${sort}&order=${sortOrder}&${search}`
    // ).then((res) => {
    //   dispatch(setItems(res.data));
    //   dispatch(setIsLoading(false));
    //   console.log(""); //  -  
    // });

    //        JS-.   JS
    // try {
    //   //  -
    //   // const res = await Axios.get(
    //   //   `https://66028e549d7276a755538691.mockapi.io/items?page=${currentPage}&limit=4&${category}&sortBy=${sort}&order=${sortOrder}&${search}`
    //   // );
    //   dispatch(
    //     fetchProducts({
    //       category,
    //       sort,
    //       sortOrder,
    //       search,
    //       currentPage,
    //     })
    //   );
    // } catch (error) {
    //   console.log(error);
    // }
    // try, catch  finally      redux

    //finally {
    //        
    //        
    //setIsLoading(false);
    //}

    dispatch(
      fetchProducts({
        category,
        sort,
        sortOrder,
        search,
        currentPage,
      })
    );

    window.scrollTo(0, 0);
  };

  //const [items, setItems] = React.useState([]);

  // const [categoryId, setCategoryId] = React.useState(0);
  // const [sortType, setSortType] = React.useState({
  //   name: "",
  //   sortProperty: "rating", // , sortProperty -    ,   , , 
  // });
  // const [currentPage, setCurrentPage] = React.useState(1);

  //     URL
  //  - ,      
  React.useEffect(() => {
    if (isMounted.current) {
      const queryString = qs.stringify({
        sortType,
        categoryId,
        currentPage,
      });
      navigate(`?${queryString}`); //   
    }
    isMounted.current = true;
    // ,      ,  false       ,
    //    true,       
  }, [categoryId, sortType, currentPage]);

  React.useEffect(() => {
    //   ,  
    //    
    //  ,    
    if (window.location.search) {
      const params = qs.parse(window.location.search.substring(1));
      const sort = list.find((obj) => obj.sortProperty === params.sortType);
      dispatch(setFilter({ ...params, sort }));
      isAxios.current = true; //     ,     URL    
    }
  }, []);

  React.useEffect(() => {
    //setIsLoading(true);
    // const category = categoryId > 0 ? `category=${categoryId}` : "";
    // //    "" -     1  n - categories.length
    // const sort = sortType.replace("-", ""); // ,    
    // const sortOrder = sortType.includes("-") ? "asc" : "desc"; //  ,  
    // const search = searchValue ? `search=${searchValue}` : ""; //     ,      

    // fetch(
    //   `https://66028e549d7276a755538691.mockapi.io/items?page=${currentPage}&limit=4&${category}&sortBy=${sort}&order=${sortOrder}&${search}`
    // )
    //   .then((res) => {
    //     return res.json();
    //   })
    //   .then((arr) => {
    //     dispatch(setItems(arr));
    //     dispatch(setIsLoading(false)); //   -   ,   ,  set(false)
    //   });

    // Axios.get(
    //   `https://66028e549d7276a755538691.mockapi.io/items?page=${currentPage}&limit=4&${category}&sortBy=${sort}&order=${sortOrder}&${search}`
    // ).then((res) => {
    //   dispatch(setItems(res.data));
    //   dispatch(setIsLoading(false));
    // });

    if (!isAxios.current) {
      //        
      //     
      axiosProducts();
    }
    isAxios.current = false;
  }, [categoryId, sortType, searchValue, currentPage]); //   ,    

  const skelets = [...new Array(4)].map((_, index) => <Skeleton key={index} />);
  // const products = items
  //   .filter((obj) => {
  //     if (obj.name.toLowerCase().includes(searchValue)) {
  //       return true;
  //     }
  //     return false;
  //   })
  //   .map((obj) => <PelmeniBlock key={obj.id} {...obj} />); //  ,    
  //   ,        

  const products = items.map((obj) => <PelmeniBlock key={obj.id} {...obj} />);

  if (webStatus === "error") {
    return <NoName />;
  }

  return (
    <>
      <div className="content__top">
        <Categories
          value={categoryId} //   ,      Category
          onClickCategory={onChangeCategory} //   id,  
        />
        <Sort />
      </div>
      <h2 className="content__title"> </h2>
      <div className="content__items">
        {webStatus === "loading" ? skelets : products}
      </div>
      <Pagination
        currentPage={currentPage}
        onChangePage={onChangePageNumber} //   ,
        //  currentPage,     page={currentPage}
      />
    </>
  );
};

export default Home;
